@import "./../../../globals";

select {
  border: none;
  border-radius: 32px;
  padding: 10px 16px;
  background: none;
  background-color: $color4;
  font-size: 0.8em;
  font-weight: 500;
  color: #FFFFFF;
  appearance: none;
  text-indent: 1px;
  cursor: pointer;
  text-overflow: '';
  text-align: center;
  display: inline;
  text-align-last: center;
  transition: box-shadow 0.4s;
  box-shadow: rgba($color4, 0.4) 0px 0px 15px 0px;
  outline: none;
  &:hover {
    box-shadow: rgba($color4, 0.2) 0px 0px 20px 10px;
  }
}
