@import 'globals';

html {
  box-sizing: border-box;
  font-size: 14px;
}

* {
  &, &:before, &:after {
    box-sizing: inherit;
  }
}

.wrapper {
  font-family: 'Roboto', sans-serif;
  width: 100%;
  min-height: 100vh;
  background-color: $color3;
  position: relative;
}

.app {
  width: 100%;
  section {
    padding: 14px 30px;
    p {
      & > label {
        font-size: 0.9em;
        font-weight: 500;
        text-transform: uppercase;
        display: inline-block;
        margin-right: 14px;
        user-select: none;
      }
      span {
        display: inline-block;
        input[type="checkbox"] {
          display: none;
          &:checked ~ label {
            font-weight: 500;
            border-bottom: 1px dashed $color2;
          }
        }
        label {
          color: $color2;
          font-size: 0.84em;
          cursor: pointer;
          user-select: none;
          outline: none;
        }
      }
    }
    input[type="text"], input[type="url"], input[type="password"] {
      display: block;
      width: 100%;
      padding: 12px 12px;
      outline: none;
      font-size: 1em;
      border: none;
      border-radius: 4px;
      box-shadow: rgba($color1, 0.03) 0px 0px 8px 2px;
      background-color: #FFFFFF;
      position: relative;
      display: block;
      transition: box-shadow 0.4s;
      &:focus, &:active {
        box-shadow: rgba($color2, 0.4) 0px 0px 50px 8px;
      }
    }
  }
}

@include desktop {
  .wrapper {
    background-color: $color2;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    @include grained(0.4);
  }

  .app {
    background-color: $color3;
    border-radius: 10px;
    max-width: 700px;
    overflow: hidden;
    position: relative;
    box-shadow: rgba($color1, 0.1) 0px 0px 40px 30px;
    section {
      padding: 14px 60px;
    }
  }
}
