@import "./../../globals";

section.salted-password {
  padding: 16px 30px 30px !important;
  margin-top: 20px;
  background-color: $color4;
  box-shadow: rgba($color4, 0.3) 0px -10px 40px 10px;
  @include grained(0.4);
  p {
    & > label {
      color: rgba(#FFFFFF, 0.8);
    }
    span {
      label {
        color: rgba(#FFFFFF, 0.7) !important;
      }
      input[type="number"] {
        border: none;
        margin: 0 10px;
        width: 36px;
        font-size: 12px;
        font-weight: 500;
        background: none;
        color: rgba(#FFFFFF, 0.5);
        border-bottom: 1px dashed rgba(#FFFFFF, 0.5);
      }
    }
  }
  #salted-password {
    color: transparent;
    cursor: text;
    text-shadow: 0 0 5px $color4;
    border-radius: 32px;
    display: block;
    text-align: center;
    padding: 10px 24px;
    min-height: 36px;
    background-color: rgba(#FFFFFF, 0.3);
    font-weight: 500;
    font-size: 14px;
    outline: none;
    box-shadow: rgba($color1, 0.1) 0px 0px 40px 10px;
  }
}

@include desktop {
  section.salted-password {
    margin-top: 30px;
  }
}