$color1: rgba(43, 45, 66, 1);
$color2: rgba(141, 153, 174, 1);
$color3: rgba(237, 242, 244, 1);
$color4: #1d3557;

$desktop-width: 768px;

@mixin desktop {
  @media (min-width: #{$desktop-width}) {
    @content;
  }
}

@mixin grained($opacity) {
  z-index: 0;
  position: relative;
  &:after {
    content : '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    background-image: url(data:image/png;base64,); 
    width: 100%;
    height: 100%;
    opacity : $opacity;
    z-index: -1;
  }
}
